<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      prePosition:0,
      prePositionState:'up'
    }
  },
  computed: {
    
  },

  created: function () {


    window.onscroll = ()=> {
      let scrollTop = document.documentElement.scrollTop || window.pageYOffset
      let nowState = ''

      if(this.prePosition < scrollTop && scrollTop > 30){
        nowState = 'down'
      }else{
        nowState = 'up'
      }

      this.prePosition = scrollTop

      if(nowState!=this.prePositionState){
        this.prePositionState = nowState
        // console.log(scrollTop)
        if(nowState=='down' ){
          // document.getElementById("navbar2").style.top = "-50px";
          document.getElementById("m1").style.padding = "5px 15px";
          // document.getElementById("m2").style.padding = "5px 15px";
          // document.getElementById("h1").style['margin-top'] = "32px";
        }else{
          document.getElementById("m1").style.padding = "15px";
          // document.getElementById("m2").style.padding = "15px";
          // document.getElementById("h1").style['margin-top'] = "50px";
          // document.getElementById("navbar2").style.top = "0";
        }
      }
  };

  

  },

  methods: {
    
  }
}
</script>
