<template>
<div class="" style="margin-top:5px">

	<div class="col-md-6 offset-md-3" v-if="!isLogin">


    <div>
      <a href="https://steemit.com/kr/@koinbot/telegram-koinbot" target="_blank">Telegram Bot Add&Login Guide <small>(kor)</small></a>
    </div>


		  <fieldset style="margin-top:35px">
		    <div  class="form-group ">
		      <label for="exampleInputEmail1">ID <small class="text-muted"> (You can get ID by enter "/info" in telegram)</small></label>
		      <input type="text" class="form-control" v-model="loginData.id" placeholder="Enter ID">
		      <small id="emailHelp" class="form-text text-danger">{{loginData.idErrorMsg}}</small>
		    </div>

		    <div class="text-center">
			    <button  class="btn btn-primary" v-on:click="getLoginKey">Get Login Key</button>
		    </div>

		    <div v-if="loginData.isShowKeyInput" class="form-group">
		      <label for="exampleInputPassword1">Login Key</label>
		      <input type="number" class="form-control" v-model="loginData.key" placeholder="login key">
		      <small id="emailHelp" class="form-text text-danger">{{loginData.keyErrorMsg}}</small>
		    </div>

		    <div v-if="loginData.isShowKeyInput" class="text-center">
			    <button type="submit" class="btn btn-primary" v-on:click="loginClick">Login</button>
		    </div>
		  </fieldset>
	</div>

<!-- 334014828 -->


</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      isLogin : false,
      loginData : {id:'',key:'',idErrorMsg:'',keyErrorMsg:'',isShowKeyInput:false},
      isLoading: true
    }
  },
  computed: {
    // hasResult: function () {
    //   return this.posts.length > 0
    // }

  },

	ready: function(){// this method will be invoked every time after the router-view is re-renderered
        console.log('READYYYYY')
    },

	created: function () {
		this.$store.commit('topMenu','noti')
		this.loginData.isShowKeyInput = false;

		let telegram_id = localStorage.getItem('telegram_id')

		this.isLogin =  telegram_id? true: false

		this.loginData.id = telegram_id



		console.log(this.$route.params.tid);
		console.log(this.$route.params.tid);

		let paramId = this.$route.params.tid;

		if(paramId){
			this.loginData.id = paramId;
		}


		// localStorage.removeItem('telegram_id');

		// this.$route.router.go('/');

		console.log('created');
	},

  methods: {
    searchTerm: function () {

        this.isLoading = true;

        this.$http.get('http://utillab.com:8886/coin/ticker/all')
        .then((result) => {
          // console.log(result)
          this.setMcap(result.data);
          this.setBithumb(result.data);
          this.setUpbit(result.data);
          this.isLoading = false;

        })
        .catch(error => {
          this.isLoading = false;
          console.log(error.response)
        });

    },



    getLoginKey(val){
	   	// var json = JSON.stringify(this.loginData)
    	if(this.loginData.id==''){
    		this.loginData.idErrorMsg ='Please input ID.'
    		return
    	}

        this.$http.put('http://utillab.com:8886/coin/telegram/loginkey',this.loginData)
        .then((result) => {
          console.log(result.data)
          if(result.data.message=='NotFoundUser'){
          	this.loginData.idErrorMsg ='ID not found. Please check your ID or /start.'
          }else{
          	this.loginData.isShowKeyInput = true;
          }
        })
        .catch(error => {
          console.log(error.response)
        });
    },


    loginClick:function(val){

    	if(this.loginData.key==''){
    		this.loginData.keyErrorMsg ='Enter the key you received in the telegram.'
    		return
    	}

        this.$http.post('http://utillab.com:8886/coin/telegram/login',this.loginData)
        .then((result) => {
          if(result.data.message=='DoNotEqual'){
          	this.loginData.keyErrorMsg ='The login key does not match. Please check it.'
          }else if(result.data.message=='Success'){
          	localStorage.setItem('telegram_id', this.loginData.id);
          	// this.$router.push('/noti')
          	this.isLogin = true
  			this.$bus.$emit('loginRefresh', 1)
          	// location.href="notice/List.jsp";

          }
        })
        .catch(error => {
          console.log(error.response)
        });

    },

    toFixed1:function(val){
      return Number(val).toFixed(1)
    },

    toFixed0:function(val){
      return Number(val).toFixed(0)
    },

    chageColor:function(val){
      let text = 'text-blue'
      if(Number(val) < 0)
        text = 'text-red'
      return text
    }



  }
}
</script>


