<template>
<div class="addpercentgroup">
	<div class="btn-group " style="margin-top:5px" role="group" aria-label="Basic example">
	  <button type="button" class="btn " v-on:click.prevent="clickOne('-15')" ><small>-15</small></button>
	  <button type="button" class="btn " v-on:click.prevent="clickOne('-10')" ><small>-10</small></button>
	  <button type="button" class="btn " v-on:click.prevent="clickOne('-5')" ><small>-5</small></button>
	  <button type="button" class="btn " v-on:click.prevent="clickOne('0')" ><small>0</small></button>
	  <button type="button" class="btn " v-on:click.prevent="clickOne('+5')" ><small>+5</small></button>
	  <button type="button" class="btn " v-on:click.prevent="clickOne('+10')" ><small>+10</small></button>
	  <button type="button" class="btn " v-on:click.prevent="clickOne('+15')" ><small>+15</small></button>
	</div>
</div>
</template>

<script>
export default {

	props: ['exchange'],
	methods: {
	    clickOne: function (val) {
	      console.log(this.exchange + '  clickOne : ' + val)
	      let obj = {exchange:this.exchange}

	      if(val=='-15'){
	      	obj.value = 0.15;
	      	obj.sign = '-';
	      }else if(val=='-10'){
			obj.value = 0.1;
	      	obj.sign = '-';
	      }else if(val=='-5'){
			obj.value = 0.05;
	      	obj.sign = '-';
	      }else if(val=='0'){
			obj.value = 0;
	      	obj.sign = '';
	      }else if(val=='+5'){
			obj.value = 0.05;
	      	obj.sign = '+';
	      }else if(val=='+10'){
			obj.value = 0.1;
	      	obj.sign = '+';
	      }else if(val=='+15'){
			obj.value = 0.15;
	      	obj.sign = '+';
	      }

	      this.$emit('clickOne',obj)
	    }
	}
}
</script>



