<template>
<div class="" >


	<div class="col-md-6 offset-md-3" >
	    <h4><b>KoinBot </b> <small class="text-muted"> beta</small></h4>
	    <small class="form-text text-muted">for koinbot notification setting in telegram.</small>
	    <div v-if="userData.telegram"> {{userData.telegram.first_name}} {{userData.telegram.last_name}} </div>
	    <br/><br/>
	</div>

	
	<div class="col-md-6 offset-md-3 " style="margin-top:-15px" v-if="notiData">

		<h5 >1. Price</h5>
		<div class="card border-light mb-3" style="">

		<div class="card-header my-card-header"><small class="text-muted">exchange</small></div>
		<div class="card-body" style="margin-bottom:-10px;margin-top:-10px">
			<button type="button" class="btn btn-sm" v-on:click.prevent="priceExchangeClick('bithumb')" v-bind:class="{'btn-primary':inputData.priceExchange=='bithumb'}">Bithumb</button>
			<button type="button" class="btn btn-sm" v-on:click.prevent="priceExchangeClick('upbit')" v-bind:class="{'btn-primary':inputData.priceExchange=='upbit'}">Upbit</button>

			<button type="button" class="btn btn-sm" v-on:click.prevent="priceExchangeClick('bitfinex')" v-bind:class="{'btn-primary':inputData.priceExchange=='bitfinex'}">Bitfinex</button>
		</div>

		<div class="card-header my-card-header"><small class="text-muted">price margin 0.5%</small></div>
		<div class="card-body">

		<div class="form-group form-inline" style="margin-top:-10px">
		    <div class="custom-control custom-radio m-r-15 "  v-for="(value,index) in coinList">
		      <input type="radio" v-bind:id="'priceValue'+index" v-on:change="priceSymbolChange" v-bind:value="value" v-model="inputData.priceSymbol" name="customRadio" class="custom-control-input" >
		      <label class="custom-control-label" v-bind:for="'priceValue'+index">{{value}}</label>
		    </div>
	    </div>

		<div class="form-group" style="margin-top:-15px">
		  <div class="form-group">
		  	<div class="text-right">
			  	<small class="text-muted">{{inputData.priceValuePercent}} % </small>
		    </div>
		    <div class="input-group ">
		      <div class="input-group-prepend">
		        <span class="input-group-text "><small>{{inputData.priceSymbol}}-{{exchangePricePair}}</small></span>
		      </div>
		      <input type="number" class="form-control text-right" v-model="inputData.priceValue" aria-label="Amount (to the nearest dollar)">
		      <div class="input-group-append">
		        <button class="btn btn-outline-primary" v-on:click.prevent="priceIncreaseDecreaseClick('-')">&nbsp;-&nbsp;</button>
		        <button class="btn btn-outline-primary" v-on:click.prevent="priceIncreaseDecreaseClick('+')">&nbsp;+&nbsp;</button>
		      </div>
		    </div>
			<addPercentGroup v-on:clickOne="addPercentGroupClick"></addPercentGroup>
		  </div>
		</div>

		<div class="text-center">
			<button type="button" class="btn btn-primary btn-sm" style="padding-right:3rem;padding-left:3rem" v-on:click.prevent="priceAddClick">Add</button>
		</div>

		</div>

		<div class="card-header my-card-header"><small class="text-muted">list</small></div>
		<div class="card-body" style="margin-bottom:-20px">
			<table class="table table-hover " >
			  <tbody>
			    <tr v-if="value.function=='Price'" v-for="(value, index) in userData.telegram.notifications" v-bind:class="{ 'table-light': index%2==0 }">
			      <td style="padding:0.3rem 0.0rem 0.3rem 0.3rem;border-top:0px">{{value.exchange}}</td>
			      <td style="padding:0.3rem 0.0rem 0.3rem 0.3rem;border-top:0px">{{value.symbol}}</td>
			      <td class="td-total-style td-border0">{{ toLS(value.value) }}</td>
			      <td class="td-style td-border0" style="width:90px;">
			      	<button class="btn btn-outline-danger btn-sm" style="padding: 0.1rem 0.4rem;font-size: 0.7203125rem;" v-on:click.prevent="priceRemove(index)">remove</button>
			      </td>
			    </tr>
			  </tbody>
			</table> 
		</div>
	</div>


		<h5 >2. Technical</h5>
		<div class="card border-light mb-3" style="">
			<div class="card-header border-bottom0 padding5rem" >Bitfinex > BTC - Parabolic SAR</div>
			<div class="card-body" style="margin-bottom:-20px">
				<div class="form-inline form-group ">
					<div class=" custom-control custom-checkbox">
						<input type="checkbox" class="custom-control-input" id="customCheck1" v-on:change="notiUpdated('bitfinexBTCSAR5m',notiData.bitfinexBTCSAR5m.run)"  v-model="notiData.bitfinexBTCSAR5m.run">
						<label class="custom-control-label" for="customCheck1">5m  </label>
					</div>
					<div class=" custom-control custom-checkbox m-l-15" >
						<input type="checkbox" class="custom-control-input" id="customCheck2" v-on:change="notiUpdated('bitfinexBTCSAR15m',notiData.bitfinexBTCSAR15m.run)" v-model="notiData.bitfinexBTCSAR15m.run">
						<label class="custom-control-label" for="customCheck2">15m</label>
					</div>
					<div class=" custom-control custom-checkbox m-l-15">
						<input type="checkbox" class="custom-control-input" id="customCheck3" v-on:change="notiUpdated('bitfinexBTCSAR30m',notiData.bitfinexBTCSAR30m.run)" v-model="notiData.bitfinexBTCSAR30m.run">
						<label class="custom-control-label" for="customCheck3">30m</label>
					</div>
				</div>
			</div>

			<div class="card-header border-bottom0 padding5rem">Bithumb > All coins Up&Down</div>
			<div class="card-body" style="margin-bottom:-20px">
				<div class="form-group form-inline">
					<div class=" custom-control custom-checkbox">
						<input type="checkbox" class="custom-control-input" id="customCheck4" v-on:change="notiUpdated('bithumbAllUpDown',notiData.bithumbAllUpDown.run)" v-model="notiData.bithumbAllUpDown.run">
						<label class="custom-control-label" for="customCheck4">Up Down</label>
					</div>
					
				</div>

			</div>

		</div>

	</div>

	<router-view ></router-view>


<!-- 334014828 -->


</div>
</template>

<script>


import addPercentGroup from '@/components/AddPercentGroup'




export default {
	name: 'Noti',
	components: {
      'addPercentGroup': addPercentGroup
	},
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      isLogin : false,
      loginData : {id:'',key:'',idErrorMsg:'',keyErrorMsg:'',isShowKeyInput:false},
      isLoading: true,
      userData:{},
      notiData:undefined,
      inputData:{ priceExchange:'bithumb', priceSymbol:'BTC',priceValue:0},
      bithumbCoinList:['BTC','ETH','XRP','BCH','LTC','EOS','XMR','DASH','ETC','QTUM','BTG','ZEC'],
      bitfinexCoinList:['BTC','ETH','XRP','BCH','LTC','EOS','XMR','DASH','ETC','QTUM','BTG','ZEC'],
      upbitCoinList:['BTC','STEEM','SBD','ADA','NEO','SNT','XLM','LSK','OMG','STRAT','STORJ','WAVES','KMD'],
      coinList:[],
      allTicker:{}
    }
  },
	computed: {

		doubleValue: {
	        get(){
	            //this function will determine what is displayed in the input
	            return this.value*2;
	        },
	        set(newVal){
	            //this function will run whenever the input changes
	            this.value = newVal/2;
	        }
	    },
		exchangePricePair: function () {
			let pair = 'KRW'
			if(this.inputData.priceExchange=='bitfinex'){
				pair = 'USD'
			}
			return pair
		}


	},
	watch:{
	    $route (to, from){
	        this.show = false;
	        console.log(from)
	        console.log(to)
	        if(to.name=='Noti'){
	        	this.loginCheck()
	        }
	    },
		'inputData.priceValue':function(value) {
			this.calPricePercent(this.inputData.priceExchange)
		}
	} ,

	created: function () {
		console.log('Noti created');
		this.$store.commit('topMenu','noti')
		this.loginCheck()

		this.inputData.priceExchange = localStorage.getItem('priceExchange') || 'bithumb';

		this.getTicker()
		this.$bus.$on('loginRefresh', (id)=> {
			console.log('loginRefresh')
			this.loginCheck();
		})
	},


    mounted() {
        console.log('mounted!!')
    },

	methods: {

	    getTicker: function () {
	        this.$http.get('http://utillab.com:8886/coin/ticker/all')
	        .then((result) => {
				this.allTicker = result.data
				this.coinListUpdate(this.inputData.priceExchange)

	        })
	        .catch(error => {
	          console.log(error.response)
	        });
	    },

	    getUserInfo: function (_id) {

	        this.isLoading = true;

	        this.$http.get('http://utillab.com:8886/coin/users/telegram/' + _id)
	        .then((result) => {
	        	console.log(result.data)
	        	this.userData = result.data
	            this.isLoading = false;
	            this.setNotiData()
	        })
	        .catch(error => {
	          this.isLoading = false;
	        });

	    },


	    priceRemove(index){
	    	this.userData.telegram.notifications.splice(index,1)
			this.notiUpdated()
	    },


	    notiUpdated(type,value){
	    	console.log('noti update',type,value)
	    	// console.log(this.notiData)
	    	// console.log(this.userData.telegram.notifications)

		        this.$http.put('http://utillab.com:8886/coin/telegram/'+this.loginData.id,this.userData.telegram.notifications)
		        .then((result) => {
		          console.log(result.data)

			    	if(type){
			    		let state = value ? 'On': 'Off'
			    		this.$bus.$emit('showSnackbar', type + ' ' + state)
			    	}

		          this.setNotiData()
		        })
		        .catch(error => {
		          console.log(error.response)
		        });
	    },

	    priceAddClick(){

	    	let exchange = this.inputData.priceExchange
	    	let val = Number(this.inputData.priceValue)
	    	let symbol = this.inputData.priceSymbol || 'BTC'

	    	if(val){
	        	let newItem = {exchange:exchange,symbol:symbol,function:'Price',value:val,run:true}
	        	this.userData.telegram.notifications.push(newItem)
	        	this.notiUpdated()
	        	this.$bus.$emit('showSnackbar', exchange + ' ' + symbol + ' : ' + val.toLocaleString())
	    	}

	    },

	    priceExchangeClick(exchange){
	    	this.inputData.priceExchange = exchange
	    	this.coinListUpdate(exchange)
	    	localStorage.setItem('priceExchange',exchange)
	    },


	    addPercentGroupClick(obj){

	    	let exchange = this.inputData.priceExchange
	    	let now = 0

	    	if(exchange=='bithumb'){
	    		now = this.allTicker.bithumb[this.inputData.priceSymbol].buy_price
	    	}else if(exchange=='bitfinex'){
	    		now = this.allTicker.bitfinex[this.inputData.priceSymbol].bid
	    	}else if(exchange=='upbit'){
	    		now = this.allTicker.upbit[this.inputData.priceSymbol].tradePrice
	    	}

	    	now = Number(now)

    		let newOne = now * obj.value
    		if(obj.sign=='-'){
    			newOne = now - newOne
    		}else if(obj.sign=='+'){
    			newOne = now + newOne
    		}else{
    			newOne = now
    		}

    		if(newOne<99){
    			newOne = newOne.toFixed(1)
    		}else{
    			newOne = newOne.toFixed(0)
    		}

    		this.inputData.priceValue = Number(newOne)
	    	this.calPricePercent(exchange)

	    },

	    coinListUpdate(exchange){
	    	if(exchange=='bithumb'){
	    		this.coinList = this.bithumbCoinList
	    		this.inputData.priceSymbol = localStorage.getItem('priceSymbolBithumb') || 'BTC';
	    		this.inputData.priceValue = Number(this.allTicker.bithumb[this.inputData.priceSymbol].buy_price)
	    	}else if(exchange=='bitfinex'){
	    		this.coinList = this.bitfinexCoinList
	    		this.inputData.priceSymbol = localStorage.getItem('priceSymbolBitfinex') || 'BTC';
	    		this.inputData.priceValue = this.allTicker.bitfinex[this.inputData.priceSymbol].bid
	    	}else if(exchange=='upbit'){
	    		this.coinList = this.upbitCoinList
	    		this.inputData.priceSymbol = localStorage.getItem('priceSymbolUpbit') || 'BTC';
	    		this.inputData.priceValue = this.allTicker.upbit[this.inputData.priceSymbol].tradePrice
	    	}
	    },

		priceSymbolChange(){
			console.log('priceSymbolChange : ')

			if(this.inputData.priceExchange=='bithumb'){
				this.inputData.priceValue = this.allTicker.bithumb[this.inputData.priceSymbol].buy_price
				localStorage.setItem('priceSymbolBithumb',this.inputData.priceSymbol)
			}else if(this.inputData.priceExchange=='bitfinex'){
				this.inputData.priceValue = this.allTicker.bitfinex[this.inputData.priceSymbol].bid
				localStorage.setItem('priceSymbolBitfinex',this.inputData.priceSymbol)
			}else if(this.inputData.priceExchange=='upbit'){
				this.inputData.priceValue = this.allTicker.upbit[this.inputData.priceSymbol].tradePrice
				localStorage.setItem('priceSymbolUpbit',this.inputData.priceSymbol)
			}

		},


		calPricePercent(exchange){

			let symbol = this.inputData.priceSymbol
			let default1 = 0

			if(exchange=='bithumb'){
				default1 = Number(this.allTicker.bithumb[symbol].buy_price);
			}else if(exchange=='bitfinex'){
				default1 = Number(this.allTicker.bitfinex[symbol].bid);
			}else if(exchange=='upbit'){
				default1 = Number(this.allTicker.upbit[symbol].tradePrice);
			}

            let change = this.inputData.priceValue - default1;
            let changePer = (change / default1 )  * 100 ;
			this.inputData.priceValuePercent =  changePer.toFixed(1)	
		},


		priceIncreaseDecreaseClick(sign){

			let exchange = this.inputData.priceExchange
			let marginVal = Number(this.inputData.priceValue) *0.01
			let symbol = this.inputData.priceSymbol
			let newOne = 0

			if(sign=='-'){
				newOne = Number(this.inputData.priceValue) - marginVal
			}else if(sign=='+'){
				newOne = Number(this.inputData.priceValue) + marginVal
			}

			if(newOne<5){
    			newOne = newOne.toFixed(3)
    		}else if(newOne<100){
    			newOne = newOne.toFixed(2)
    		}else{
    			newOne = newOne.toFixed(0)
    		}

    		this.inputData.priceValue = Number(newOne)
			this.calPricePercent(exchange)

		},


	    setNotiData(){
	    	this.notiData = {}
			
	    	this.notiData.bithumbAllUpDown = this.getNotiItem('bithumb','ALL','UpDown','2m')
			this.notiData.bitfinexBTCSAR5m = this.getNotiItem('bitfinex','BTC','SAR','5m')
			this.notiData.bitfinexBTCSAR15m = this.getNotiItem('bitfinex','BTC','SAR','15m')
			this.notiData.bitfinexBTCSAR30m = this.getNotiItem('bitfinex','BTC','SAR','30m')

			// this.notiData.bitfinexBTCPrices = this.getNotiItemPrice('bitfinex','BTC','Price')


			console.log(this.notiData)
	    },

	    getNotiItem:function(_exchange,_symbol,_function,_value){
	    	let obj;

			for(var notiItem of this.userData.telegram.notifications){
				if(notiItem.symbol==_symbol && notiItem.exchange==_exchange && notiItem.function==_function && notiItem.value==_value){
    		     	obj = notiItem
    		     	break
                }
	        }

	        if(obj===undefined){
	        	let newItem = {exchange:_exchange,symbol:_symbol,function:_function,value:_value,run:false}
	        	this.userData.telegram.notifications.push(newItem)
	        	obj = newItem
	        }
	        // console.log(obj)
	        return obj
	    },


	    loginCheck(){
			let telegram_id = localStorage.getItem('telegram_id')
			this.loginData.isShowKeyInput = false;
			this.isLogin =  telegram_id? true: false
			this.loginData.id = telegram_id

			if(telegram_id){
				this.getUserInfo(telegram_id);
			}
	    },

	    toLS(val){
	      return Number(val).toLocaleString()
	    },


	    toFixed0:function(val){
	      return Number(val).toFixed(0)
	    },

	    chageColor:function(val){
	      let text = 'text-blue'
	      if(Number(val) < 0)
	        text = 'text-red'
	      return text
	    }

	}
}
</script>


